.controlCenter {
    width: 100vw;
    height: 100vh;
    background: rgb(22, 21, 21);
    background-image:url(../../public/5.jpg);
    color: red;
    font-family: "Open Sans", sans-serif;
    display: grid;
    place-items: center;
  
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  
  .controlCenter h2 {
    padding-top: 6rem;
    font-size: 3rem;
    margin-bottom: 3rem;
    color:white
  }
  
  .controlCenter h3 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    color:white
  }
  
  @font-face {
    font-family: chin;
    src: local("STKaiti"),
      local("DFKai-SB");
  }
  
  .controlCenter button {
    width: 225px;
    height: 50px;
    margin: 7px;
    border: silver 3px outset;
    border-radius: 5px;
    padding: 5px;
    font-size: 20px;
    font-family: chin,Arial;
    background: white;
    color: red;
    cursor: pointer;
  }