.LockButton{
    width: 100vw;
    height: 100vh;
    /* background-image:url(../../public/room.png); */
    background-size:cover;
    /* border-radius:50vh ; */
    /* position: fixed; */
    /* justify-content: center; */
    display: flex;
    

    justify-content: center;
    align-items: center;
}
.LockButton button{
    position: fixed;
    margin-left: 28vw;
    margin-bottom: 8vh;
    background-color: rgba(235, 77, 72, 0.53);
    width: 20vh;
    height: 20vh;
    border-radius:20vh ;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
}
.LockButton button:hover{
    background-color: rgba(235, 77, 72, 0.9);
    width: 30vh;
    height: 30vh;
    border-radius:30vh ;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.LockP{
    font-size: 5vh;
    
}

.LockButton img{
    display: flex;
    position: fixed;
    margin-top:10vh;   
    max-width: 100%;
    max-height: 80%;
    aspect-ratio: 18/10;
    z-index:-1;
}
.Locked{
    display: flex;
    position: absolute;
    height:100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: brown;
    z-index: 10;
    font-size: 100px;
}