.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: silver;
    height:50px;
    z-index: 50;
}

.toolbar a{
    white-space: nowrap;
    color:white;
    text-decoration: none;
    font-size: 2rem;
    padding-top: 3px;
    padding-left: 5px;
    display:flex;
    font-family: "STKaiti","DFKai-SB";
    float:left;
}

.toolbar button{
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height:50px;
    width:60px;
    padding-top: 13px;
    padding-bottom: 15px;
    padding-left: 15px;
    right:0px;
    background:silver;
    border:none;
    float:right;
}
.toolbar button:hover{
    background:gray;
}
.toolbar button:focus{
    outline: none;
}
.button__line{
    width: 30px;
    height: 2px;
    background: white;
}

.footer{
    color:white;
    position: fixed;
    right: 10px;
    bottom: 10px;
}
.footer p{
    font-size: large;
    text-shadow: 0 0 5px black;
}

